* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.top {
  justify-content: space-between;
}

header {
  background-color: rgb(221, 215, 215);
  padding: 5px 20px;
}

header svg {
  margin: 4px;
  font-size: 22px;
}

header p {
  margin: 1px;
}

.logo {
  width: 120px;
  height: 45px;
}

.social {
  margin: 0px 10px;
}
.social a {
  margin: 0px 6px;
}

.nav-item {
  margin: 8px 12px;
}

.C-img {
  height: 450px;
}

.mainh2 {
  font-family: sans-serif;
  font-size: 25px;
  color: #644141;
  text-align: center;
  margin: 50px 0px;
  margin-bottom: 6px;
  letter-spacing: 0.1px;
}

.mainp {
  font-family: auto;
  font-size: 18px;
  font-weight: 500;
  width: 86vw;
  margin: 5px auto;
  text-align: center;
}

.mainCards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.card {
  width: 21rem;
  height: 90%;
  margin: 20px 12px;
  padding: 10px;
  text-align: center;
}

.card img {
  margin: 14px 16px;
}

.card-body h5 {
  font-family: monospace;
  line-height: 24px;
  letter-spacing: 0.3px;
  font-weight: 600;
}

.card p {
  font-family: "Ubuntu", sans-serif;
  font-size: 15px;
  width: 90%;
  margin: 20px auto;
}

.numbers {
  /* position: relative; */
  height: 10rem;
  background-image: url('./images/numbers-bg.png');
  background-repeat: no-repeat;
  background-position: 0;
  background-size: cover;
  opacity: 0.9;
  margin: 70px 0vw;
  padding: 15px 12vw;
  justify-content: space-between;
  align-items: center;
  /* border-radius: 8px; */
  /* box-shadow: 8px 8px 13px 5px #a68989; */
}

.number-item h4 {
  font-size: 22px;
  font-family: cursive;
  font-weight: bolder;
  margin-bottom: 0px;
  text-align: center;
  color: #eaf3e8;
}

.number-item p {
  font-size: 19px;
  color: #eaf3e8;
}

.contact form{
  width: 80%;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  height: fit-content;
  background-color: #d3a8a8;
  padding: 20px 5vw;
  border-radius: 15px;
  margin: auto;
}

.contact h4{
  font-size: 25px;
  font-family: ubuntu;
  font-weight: 500;
  text-align: center;
  color: black;
  margin: 30px 0px;
}

.contact input{
  width: 28vw;
  margin: 15px;
  padding: 12px 20px;
  font-family: ubuntu;
  font-size: 17px;
  font-weight: 500;
}

.contact textarea{
  width: 63vw;
  margin: 15px;
  padding: 12px 20px;
  font-family: ubuntu;
  font-size: 17px;
  font-weight: 500;
}

footer{
  background-color: rgb(102, 98, 98);
  color: #eaf3e8;
  margin-top: 50px;
  margin-bottom: -25px;
  padding: 7px 0px;
  text-align: center;
  align-items: center;
}